.hero-img {
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;
  }
  
  .heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .hero-img::before {
    content: "";
    background: url(https://www.velocenetwork.com/wp-content/uploads/2022/12/windows-FV6Pma5U_98-unsplash.jpg);
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  
  .hero-img h1 {
    font-size: 2.4rem;
  }
  
  .hero-img p {
    font-size: 1.4rem;
    text-align: center;
  }
  
  @media screen and (max-width: 640px) {
    .hero-img h1 {
      font-size: 2rem;
    }
  }